<template>
  <div class="trans-report-wrapper container">
    <div v-if="loading" class="loading-overlay">
      <div class="overlay-content"><img src="@/assets/img/loading.gif" style="width: 60px" /></div>
    </div>
    <Header />
    <div class="content-wrapper">
      <div class="filter-toggle-wrapper" ref="toggleFilter">
        <span>
          <button class="filter-toggle" @click="toggleFilter()" :class="isFilterActive ? 'collasped' : ''"><fa :icon="['fas', 'sliders']"></fa></button>
        </span>
        <div class="filter-wrapper" :class="[isFilterActive ? 'slide-in' : 'slide-out']">
          <div class="filter-content-wrapper">
            <popup class="filter-popup-wrapper stores-popup">
              <template #header>
                <button class="filter-btn">
                  <!--<fa icon="store" />--> <span id="store-selection">{{ selectedCompanies.companyName }}</span><fa icon="angle-down" />
                </button>
              </template>
              <template #content>
                <div class="display-selection-wrapper">
                  <span v-for="company in companies" :key="company.id">
                    <input type="radio" name="store_type" :id="company.id" :value="company" v-model="selectedCompanies" @change="selectedStore" />
                    <fa class="checkmark" icon="check" />
                    <label :for="company.id">{{ company.companyName }}</label>
                  </span>
                </div>
              </template>
            </popup>
            <div class="filter-header">
              <popup class="filter-popup-wrapper">
                <template #header>
                  <button class="filter-btn"><!--<fa :icon="['far', 'file-lines']" />--> <span id="selection">Regular Transaction</span><fa icon="angle-down" /></button>
                </template>
                <template #content>
                  <div class="display-selection-wrapper">
                    <span>
                      <input type="radio" name="trans_type" value="Regular" v-model="transType" @change="selectedType" id="regular" checked="checked" />
                      <fa class="checkmark" icon="check" />
                      <label for="regular">Regular Transaction</label>
                    </span>
                    <span>
                      <input type="radio" name="trans_type" value="Pending" v-model="transType" @change="selectedType" id="pending" />
                      <fa class="checkmark" icon="check" />
                      <label for="pending">Pending Transaction</label>
                    </span>
                  </div>
                </template>
              </popup>
              <button class="search-btn btn btn-square" @click="searchWithFilter"><fa icon="magnifying-glass"></fa></button>
            </div>

            <DateType 
              @changeDateType="dateType = $event"
              @changeStartDate="startDate = $event"
              @changeEndDate="endDate = $event"
              :currentStartDate="startDate"
              :currentEndDate="endDate" />

            <Transition name="fade">
              <p v-if="invalidSearch" class="invalid-search-msg">
                <fa icon="triangle-exclamation" />
                <span>In order to have better performance,<br />please keep the date range within 45 days.</span>
              </p>
              <p v-else-if="invalidDateRange" class="invalid-search-msg">
                <fa icon="triangle-exclamation" />
                <span>Invalid date range</span>
              </p>
            </Transition>

            <div v-if="dateType == 'Single Date' || dateType == 'Today'" class="calendar-wrapper">
              <DatePicker @changeDateValue="startDate = $event" :defaultDate="startDate" labelEnableText="Date" />
            </div>
            <div v-if="dateType == 'Date Range' || dateType == 'This Week'" class="calendar-wrapper">
              <DatePicker @changeDateValue="startDate = $event" :defaultDate="startDate" :startDate="endDate" labelEnableText="From" />
              <DatePicker @changeDateValue="endDate = $event" :defaultDate="endDate" :startDate="startDate" :alwaysStartDate="true" labelEnableText="To" />
            </div>

            <div v-if="transType == 'Regular'" class="filter-content-wrapper">
              <label class="filter-label">Search By</label>
              <label><span>Stn # </span><input type="text" v-model="stn" /></label>
              <label><span>Cashier #</span><input type="text" v-model="cashier" /></label>
              <label><span>Trans # </span><input type="text" v-model="transNum" /></label>
              <label><span>Auth # </span><input type="text" v-model="authno" /></label>

              <label class="filter-label">Transaction Total($)</label>
              <span>
                <select v-model="comparedSign">
                  <option value="<">&lt;</option>
                  <option value="<=">&lt;=</option>
                  <option value="=">=</option>
                  <option value=">=">&gt;=</option>
                  <option value=">">&gt;</option>
                </select>
                <input type="text" v-model="comparedTotal" placeholder="0.00" />
              </span>
            </div>
          </div>
        </div>
      </div>
      <div class="content" v-bind:style="{ height: 'calc(' + windowHeight + 'px - 90px)' }">
        <!-- regular transaction -->
        <div v-if="regularTrans">
          <div v-if="!empty && transactions.length <= 0" class="no-results"><fa :icon="['fas', 'magnifying-glass']" />No search results found</div>
          <DataTable
            v-if="!empty && transactions.length > 0"
            id="rpt-table"
            class="tb-wrapper regular-trans"
            v-model:expandedRows="state.expandedRows"
            dataKey="trn_no"
            :value="transactions"
            scrollable
            :scrollHeight="'calc(' + windowHeight + 'px - 200px)'"
            :virtualScrollerOptions="{ itemSize: 26 }"
            :exportFilename="fileName"
            ref="dt"
          >
            <template #header>
              <!-- <div class="scroll-btn-wrapper">
                <Transition name="fade" mode="out-in">
                  <button v-if="!isScrollVisible" @click="toBottom"><fa :icon="['fas', 'arrow-down']" /></button>
                  <button v-else @click="toTop"><fa :icon="['fas', 'arrow-up']" /></button>
                </Transition>
              </div> -->

              <div class="table-header-wrapper">
                <div class="table-header">
                  <h3>{{ $t('header.' + this.$route.name) }} - {{ storeName }}</h3>
                  <p v-if="singleDateActive">As of date {{ formatDate(rptStartDate) }}</p>
                  <p v-else-if="dateRangeActive">As of date {{ formatDate(rptStartDate) }} - {{ formatDate(rptEndDate) }}</p>
                </div>
                <div class="table-btn-group">
                  <button class="print-btn btn btn-circle" onclick="window.print()"><fa icon="print"></fa></button>
                  <button class="excel-btn btn btn-circle" @click="exportCSV($event)"><fa icon="file-csv"></fa></button>
                </div>
              </div>
            </template>
            <Column class="portrait-col" field="station" header="Stn #">
              <template #body="slotProps">
                <p class="first-col">{{ slotProps.data.station }}</p>
                <p class="more-info-btn" @click.stop="onExpandRow(slotProps.data.trn_no)">
                  <span :class="[state.expandedRows[slotProps.data.trn_no] ? 'expand' : '']">
                    <button class="expand-btn"><fa icon="caret-right" /></button>
                  </span>
                  {{ slotProps.data.station }}
                </p>
              </template>
            </Column>
            <Column class="portrait-col" field="trn_no" header="Trans #">
              <template #body="slotProps">
                <a @click="getTransDetail(slotProps.data.station, slotProps.data.trn_no)">{{ slotProps.data.trn_no }}</a>
              </template>
            </Column>
            <Column class="portrait-col" field="order_time" header="Date">
              <template #body="slotProps">
                {{ formatDate(slotProps.data.order_time) }}
              </template>
            </Column>
            <Column field="auth_no" header="Auth #" />
            <Column field="cashier_no" header="Cashier" />
            <Column field="paytype" header="Paytype" />
            <Column field="discount" header="Discount($)" />
            <Column field="supervisor" header="Supervisor" />
            <Column field="amount" header="Amount($)" class="amount portrait-col">
              <template #body="slotProps">
                {{ formatPrice(slotProps.data.amount) }}
              </template>
            </Column>
            <template #expansion="slotProps">
              <div class="more-info-wrapper">
                <p class="landscape"><span class="bold-600">Auth #</span> {{ slotProps.data.auth_no }}</p>
                <p class="landscape"><span class="bold-600">Cashier</span> {{ slotProps.data.cashier_no }}</p>
                <p class="landscape"><span class="bold-600">Paytype</span> {{ slotProps.data.paytype }}</p>
                <p><span class="bold-600">Discount($)</span> {{ formatPrice(slotProps.data.discount) }}</p>
                <p><span class="bold-600">Supervisor</span> {{ slotProps.data.supervisor }}</p>
              </div>
            </template>
            <ColumnGroup type="footer">
              <Row>
                <Column class="dt-footer" footer="Number of Transactions:" :colspan="2" />
                <Column class="dt-footer" :footer="formatNumber(transCount)" footerStyle="text-align:left" />
                <Column class="dt-footer" footer="Total Amount($):" :colspan="5" footerStyle="text-align:right" />
                <Column class="dt-footer" :footer="formatPrice(transTotal)" :colspan="2" />
              </Row>
            </ColumnGroup>
            <template #footer>
              <div class="mobile-dt-footer">
                <p class="bold-600">Number of Transactions: {{ formatNumber(transCount) }}</p>
                <p class="bold-600">Total Amount($): {{ formatPrice(transTotal) }}</p>
              </div>
            </template>
          </DataTable>

          <!-- regular transaction detail -->
          <Transition name="fade">
            <div v-if="transDetail">
              <div class="overlay-black"></div>
              <div class="popup-wrapper">
                <div class="popup-header">
                  <p class="text">Transaction Detail</p>
                  <button class="close-btn" @click="closePopup"><fa icon="xmark" /></button>
                </div>
                <div class="trans-detail-wrapper">
                  <ul>
                    <li><span>Transaction #</span> {{ transactionDetailData.trn_no }}</li>
                    <li><span>Station #</span> {{ transactionDetailData.station }}</li>
                    <li><span>Amount</span> {{ formatPrice(transactionDetailData.amount) }}</li>
                    <li><span>Food Stamp</span> {{ formatPrice(transactionDetailData.fs_collect) }}</li>
                    <li><span>Debit Card</span> {{ formatPrice(transactionDetailData.debit) }}</li>
                    <li><span>Others</span> {{ formatPrice(transactionDetailData.other_amt) }}</li>
                    <li><span>Supervisor</span> {{ formatPrice(transactionDetailData.supervisor) }}</li>
                    <li><span>Payment Type</span> {{ transactionDetailData.paytype }}</li>
                    <li><span>Cashier</span> {{ transactionDetailData.cashier_no }}</li>
                    <li><span>Tax</span> {{ formatPrice(transactionDetailData.tax) }}</li>
                    <li><span>EBT Cash</span> {{ formatPrice(transactionDetailData.ebtcash) }}</li>
                    <li><span>Check</span> {{ formatPrice(transactionDetailData.check_pay) }}</li>
                    <li><span>Gift Card</span> {{ formatPrice(transactionDetailData.gift_card) }}</li>
                    <li><span>Auth. #</span> {{ transactionDetailData.auth_no }}</li>
                    <li><span>Date</span> {{ formatDate(transactionDetailData.order_time) }}</li>
                    <li><span>Discount</span> {{ formatPrice(transactionDetailData.discount) }}</li>
                    <li><span>Cash</span> {{ formatPrice(transactionDetailData.cash_pay) }}</li>
                    <li><span>Credit Card</span> {{ formatPrice(transactionDetailData.credit_amt) }}</li>
                    <li><span>Store Credit</span> {{ formatPrice(transactionDetailData.store_credit_amt) }}</li>
                    <div class="btn-group">
                      <button class="print-btn btn btn-circle" onclick="window.print()"><fa icon="print"></fa></button>
                      <button class="excel-btn btn btn-circle" @click="exportCSV($event)"><fa icon="file-excel"></fa></button>
                    </div>
                  </ul>
                </div>

                <DataTable
                  id="rpt-table"
                  v-model:expandedRows="state.expandedRows"
                  dataKey="item_no"
                  :value="transactionDetailData.details"
                  scrollable
                  scrollHeight="360px"
                  :exportFilename="fileNameDetail"
                  ref="dt"
                >
                  <Column field="qty" header="Qty">
                    <template #body="slotProps">
                      <p class="first-col">{{ slotProps.data.qty }}</p>
                      <p class="more-info-btn" @click.stop="onExpandRow(slotProps.data.item_no)">
                        <span :class="[state.expandedRows[slotProps.data.item_no] ? 'expand' : '']">
                          <button class="expand-btn"><fa icon="caret-right" /></button>
                        </span>
                        {{ slotProps.data.qty }}
                      </p>
                    </template>
                  </Column>
                  <Column field="item_no" header="Item #" />
                  <Column field="item_amt" header="Amount($)" class="amount portrait-col">
                    <template #body="slotProps">
                      {{ formatPrice(slotProps.data.item_amt) }}
                    </template>
                  </Column>
                  <Column field="item_dep" header="Dep #" />
                  <Column field="name_desc" header="Name/Description" class="name-desc portrait-col" />
                  <Column field="pack" header="Pack" />
                  <Column field="weight" header="Weight (lb)" />
                  <Column field="misc" header="Misc" />
                  <Column field="msg" header="Message" />

                  <template #expansion="slotProps">
                    <div class="more-info-wrapper">
                      <p class="landscape"><span class="bold-600">Item #</span> {{ slotProps.data.item_no }}</p>
                      <p class="landscape"><span class="bold-600">Department</span> {{ slotProps.data.item_dep }}</p>
                      <p v-if="slotProps.data.pack" class="landscape"><span class="bold-600">Pack</span> {{ slotProps.data.pack }}</p>
                      <p v-if="slotProps.data.weight"><span class="bold-600">Weight (lb)</span> {{ slotProps.data.weight }}</p>
                      <p v-if="slotProps.data.misc"><span class="bold-600">Misc</span> {{ slotProps.data.misc }}</p>
                      <p v-if="slotProps.data.msg"><span class="bold-600">Message</span> {{ slotProps.data.msg }}</p>
                    </div>
                  </template>
                </DataTable>
              </div>
            </div>
          </Transition>
        </div>

        <!-- pending transaction -->
        <div v-if="pendingTrans">
          <div v-if="!empty && transactionsPending.length <= 0" class="no-results"><fa :icon="['fas', 'magnifying-glass']" />No search results found</div>
          <DataTable
            v-if="!empty && transactionsPending.length > 0"
            class="tb-wrapper pending"
            v-model:expandedRows="state.expandedRows"
            dataKey="trn_no"
            :value="transactionsPending"
            scrollable
            :scrollHeight="'calc(' + windowHeight + 'px - 200px)'"
            :virtualScrollerOptions="{ itemSize: 26 }"
            :exportFilename="fileName"
            ref="dt"
          >
            <template #header>
              <div class="table-header-wrapper">
                <div class="table-header">
                  <h3>Pending Transaction Report - {{ selectedCompanies.companyName }}</h3>
                  <p v-if="singleDateActive">As of date {{ formatDate(rptStartDate) }}</p>
                  <p v-else-if="dateRangeActive">As of date {{ formatDate(rptStartDate) }} - {{ formatDate(rptEndDate) }}</p>
                </div>
                <div class="table-btn-group">
                  <button class="print-btn btn btn-circle" onclick="window.print()"><fa icon="print"></fa></button>
                  <button class="excel-btn btn btn-circle" @click="exportCSV($event)"><fa icon="file-excel"></fa></button>
                </div>
              </div>
            </template>
            <Column field="order_time" header="Date" class="portrait-col">
              <template #body="slotProps">
                <p class="first-col">{{ formatDate(slotProps.data.order_time) }}</p>
                <p class="more-info-btn" @click.stop="onExpandRow(slotProps.data.trn_no)">
                  <span :class="[state.expandedRows[slotProps.data.trn_no] ? 'expand' : '']">
                    <button class="expand-btn"><fa icon="caret-right" /></button>
                  </span>
                  {{ formatDate(slotProps.data.order_time) }}
                </p>
              </template>
            </Column>
            <Column field="station" header="Stn #" />
            <Column field="trn_no" header="Trans #" class="portrait-col">
              <template #body="slotProps">
                <a @click="getTransDetail(slotProps.data.station, slotProps.data.trn_no)">{{ slotProps.data.trn_no }}</a>
              </template>
            </Column>
            <Column field="cashier_no" header="Cashier" />
            <Column field="status" header="Status" />
            <Column field="total" header="Amount($)" class="amount portrait-col">
              <template #body="slotProps">
                {{ formatPrice(slotProps.data.total) }}
              </template>
            </Column>
            <Column field="deposit" header="Deposit" class="deposit"></Column>

            <template #expansion="slotProps">
              <div class="more-info-wrapper">
                <p class="landscape"><span class="bold-600">Stn #</span> {{ slotProps.data.station }}</p>
                <p class="landscape"><span class="bold-600">Cashier</span> {{ slotProps.data.cashier_no }}</p>
                <p class="landscape"><span class="bold-600">Status</span> {{ slotProps.data.status }}</p>
                <p class="landscape"><span class="bold-600">Deposit($)</span> {{ formatPrice(slotProps.data.deposit) }}</p>
              </div>
            </template>

            <ColumnGroup type="footer">
              <Row>
                <Column class="dt-footer" footer="Number of Transactions:" :colspan="2" />
                <Column class="dt-footer" :footer="formatNumber(transCount)" footerStyle="text-align:left" />
                <Column class="dt-footer" footer="Total($):" :colspan="2" footerStyle="text-align:right" />
                <Column class="dt-footer" :footer="formatPrice(totalPendingTransAmount)" :colspan="1" />
                <Column class="dt-footer" :footer="formatPrice(totalPendingTransDeposit)" :colspan="2" />
              </Row>
            </ColumnGroup>
            <template #footer>
              <div class="mobile-dt-footer">
                <p class="bold-600">Number of Transactions: {{ formatNumber(transCount) }}</p>
                <p class="bold-600">Total Amount($): {{ formatPrice(totalPendingTransAmount) }}</p>
                <p class="bold-600">Total Deposit($): {{ formatPrice(totalPendingTransDeposit) }}</p>
              </div>
            </template>
          </DataTable>

          <!-- pending transaction detail-->
          <Transition name="fade">
            <div v-if="transDetail">
              <div class="overlay-black"></div>
              <div class="popup-wrapper">
                <div class="popup-header">
                  <p class="text">Pending Transaction Detail</p>
                  <button class="close-btn" @click="closePopup"><fa icon="xmark" /></button>
                </div>
                <div class="trans-detail-wrapper">
                  <ul>
                    <li><span>Transaction #</span> {{ transactionPendingDetailData.trn_no }}</li>
                    <li><span>Station #</span> {{ transactionPendingDetailData.station }}</li>
                    <li><span>Payment Type</span></li>
                    <li><span>Cashier</span> {{ transactionPendingDetailData.cashier_no }}</li>
                    <li><span>Date</span> {{ formatDate(transactionPendingDetailData.order_time) }}</li>
                    <li><span>Amount($)</span> {{ formatPrice(transactionPendingDetailData.total) }}</li>
                    <li><span>Deposit($)</span> {{ formatPrice(transactionPendingDetailData.deposit) }}</li>
                    <div class="btn-group">
                      <button class="print-btn btn btn-circle" onclick="window.print()"><fa icon="print"></fa></button>
                      <button class="excel-btn btn btn-circle" @click="exportCSV($event)"><fa icon="file-excel"></fa></button>
                    </div>
                  </ul>
                </div>
                <DataTable
                  id="rpt-table"
                  v-model:expandedRows="state.expandedRows"
                  dataKey="item_no"
                  :value="transactionPendingDetailData.posTrnsPendingDetails"
                  scrollable
                  scrollHeight="360px"
                  :exportFilename="fileNameDetail"
                  ref="dt"
                >
                  <Column field="qty" header="Qty">
                    <template #body="slotProps">
                      <p class="first-col">{{ slotProps.data.qty }}</p>
                      <p class="more-info-btn" @click.stop="onExpandRow(slotProps.data.item_no)">
                        <span :class="[state.expandedRows[slotProps.data.item_no] ? 'expand' : '']">
                          <button class="expand-btn"><fa icon="caret-right" /></button>
                        </span>
                        {{ slotProps.data.qty }}
                      </p>
                    </template>
                  </Column>
                  <Column field="item_no" header="Item #" />
                  <Column field="price" header="Amount($)" class="amount portrait-col">
                    <template #body="slotProps">
                      {{ formatPrice(slotProps.data.price) }}
                    </template>
                  </Column>
                  <Column field="item_dep" header="Dep #" />
                  <Column field="name_desc" header="Name/Description" class="name-desc portrait-col" />
                  <Column field="pack" header="Pack" />
                  <Column field="weight" header="Weight (lb)" />
                  <Column field="misc" header="Misc" />
                  <Column field="msg" header="Message" />

                  <template #expansion="slotProps">
                    <div class="more-info-wrapper">
                      <p class="landscape"><span class="bold-600">Item #</span> {{ slotProps.data.item_no }}</p>
                      <p class="landscape"><span class="bold-600">Department</span> {{ slotProps.data.item_dep }}</p>
                      <p v-if="slotProps.data.pack" class="landscape"><span class="bold-600">Pack</span> {{ slotProps.data.pack }}</p>
                      <p v-if="slotProps.data.weight"><span class="bold-600">Weight (lb)</span> {{ slotProps.data.weight }}</p>
                      <p v-if="slotProps.data.misc"><span class="bold-600">Misc</span> {{ slotProps.data.misc }}</p>
                      <p v-if="slotProps.data.msg"><span class="bold-600">Message</span> {{ slotProps.data.msg }}</p>
                    </div>
                  </template>
                </DataTable>
              </div>
            </div>
          </Transition>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import { reactive } from "vue";
import axios from "axios";
import moment from "moment";
import Header from "@/components/Header.vue";
import Popup from "@/components/Popup.vue";
import DatePicker from "@/components/DatePicker.vue";
import DateType from "@/components/DateType.vue";

export default {
  components: {
    Header,
    Popup,
    DatePicker,
    DateType
  },
  setup() {
    const state = reactive({
      expandedRows: {},
    });

    const onExpandRow = (id) => {
      const checkProp = id in state.expandedRows;
      if (checkProp) {
        delete state.expandedRows[id];
      } else {
        state.expandedRows[id] = true;
      }
      state.expandedRows = { ...state.expandedRows, };
    };

    return {
      state,
      onExpandRow,
    };
  },
  data() {
    return {
      windowHeight: window.innerHeight,
      isFilterActive: true,
      empty: true,
      loading: false,
      companies: [
      {
        id: 1027,
        companyName: "Apple Supermarket 1"
        },
        {
        id: 1221,
        companyName: "Apple Supermarket 2"
        },
      ],
      transactions: [],
      transactionDetailData: [],
      transactionsPending: [],
      transactionPendingDetailData: [],
      transCount: "",
      transTotal: "",
      startDate: "",
      endDate: "",
      rptStartDate: "",
      rptEndDate: "",
      transDetail: "",
      transType: "Regular",
      dateType: "Date Range",
      regularTrans: true,
      pendingTrans: false,
      isScrollVisible: false,
      invalidSearch: false,
      invalidDateRange: false,
      singleDateActive: false,
      dateRangeActive: false,
      stn: "",
      cashier: "",
      transNum: "",
      comparedSign: "=",
      comparedTotal: "",
      authno: "",
      selectedCompanies: [],
      storeName: "",
    };
  },
  mounted() {
    // this.companies = JSON.parse(localStorage.getItem("companies"));

    this.selectedCompanies = this.companies[0];

    this.$nextTick(() => {
      window.addEventListener("resize", this.onResize);
    });
  },
  // beforeMount() {
  //   window.addEventListener("click", this.close);
  // },
  // unmounted() {
  //   window.removeEventListener("click", this.close);
  // },
  beforeDestroy() {
    window.removeEventListener("resize", this.onResize);
  },
  methods: {
    onResize() {
      this.windowHeight = window.innerHeight;
    },
    toTop() {
      const table = document.getElementsByClassName("p-datatable-scrollable-table")[0];
      table.scrollIntoView({ behavior: "smooth", block: "start" });
      this.isScrollVisible = !this.isScrollVisible;
    },
    toBottom() {
      // const table = document.getElementsByClassName("p-datatable-scrollable-table")[0];
      const table = document.querySelector(".p-datatable-scrollable-table");
      table.scrollIntoView({ behavior: "smooth", block: "end" });
      this.isScrollVisible = !this.isScrollVisible;
    },
    toggleFilter() {
      this.isFilterActive = !this.isFilterActive;
    },
    // close(e) {
    //   if (!this.$refs.toggleFilter.contains(e.target)) {
    //     this.isFilterActive = false;
    //   }
    // },
    formatPrice(value) {
      if (!value || value == 0) {
        return "0.00";
      }
      let val = (value / 1).toFixed(2).replace(",", ".");
      return val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
    formatNumber(value) {
      if (!value) {
        return 0;
      }
      return value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
    formatDate(date) {
      return moment(date).format("MMM DD, yyyy");
    },
    exportCSV() {
      this.$refs.dt.exportCSV();
    },
    selectedType() {
      var value = "Nothing selected",
        selected = document.querySelector('input[name="trans_type"]:checked'),
        selection = document.querySelector("#selection");
      if (selected) {
        value = document.querySelector('label[for="' + selected.id + '"]').innerHTML;
      }
      selection.innerHTML = value;
    },
    selectedStore() {
      var value = "Nothing selected",
        selected = document.querySelector('input[name="store_type"]:checked'),
        selection = document.querySelector("#store-selection");
      if (selected) {
        value = document.querySelector('label[for="' + selected.id + '"]').innerHTML;
      }
      selection.innerHTML = value;
    },
    searchWithFilter() {
      this.loading = true;
      this.isFilterActive = false;
      this.transactions = [];
      this.transactionsPending = [];
      this.storeName = this.selectedCompanies.companyName;

      this.rptStartDate = "";
      this.rptEndDate = ""

      if (this.dateType == 'Single Date' || this.dateType == 'Today'){
        this.rptStartDate = moment(this.startDate).format("YYYY-MM-DD");
        this.rptEndDate = moment(this.startDate).format("YYYY-MM-DD");
        this.singleDateActive = true;
        this.dateRangeActive = false;
      } else if (this.dateType == 'Date Range' || this.dateType == 'This Week'){
        this.dateRangeActive = true;
        this.singleDateActive = false;
        const days = Math.round((this.endDate - this.startDate) / (1000 * 60 * 60 * 24));
        if (days > 45) {
          this.loading = false;
          this.isFilterActive = true;
          this.invalidSearch = true;
          this.invalidDateRange = false;
        } else if (days < 0) {
          this.loading = false;
          this.isFilterActive = true;
          this.invalidSearch = false;
          this.invalidDateRange = true;
          this.rptStartDate = "";
          this.rptEndDate = "";
        } else {
          this.invalidSearch = false;
          this.invalidDateRange = false;
          this.rptStartDate = moment(this.startDate).format("YYYY-MM-DD");
          this.rptEndDate = moment(this.endDate).format("YYYY-MM-DD");
        }
      }

      if (this.transType == "Regular") {
        this.regularTrans = true;
        this.pendingTrans = false;

        axios
          .get("https://cloudbackofficereportapidev.azurewebsites.net/Report/Transactions", {
            headers: {
              marketid: this.selectedCompanies.id,
            },
            params: {
              sDate: this.rptStartDate,
              eDate: this.rptEndDate,
              stn: this.stn == "" ? 0 : this.stn,
              cashier: this.cashier == "" ? 0 : this.cashier,
              comparedTotal: this.comparedTotal,
              comparedSign: this.comparedSign,
              authno: this.authno,
              transCode: this.transNum,
            },
          })
          .then((response) => {
            this.transactions = response.data.datas.posTrnsLists;
            this.transCount = response.data.datas.transCount;
            this.transTotal = response.data.datas.transTotal;
          })
          .catch((error) => console.log(error.response))
          .finally(() => {
            this.loading = false;
            this.empty = false;
          });
      } else if (this.transType == "Pending") {
        this.regularTrans = false;
        this.pendingTrans = true;

        axios
          .get("https://cloudbackofficereportapidev.azurewebsites.net/Report/Transactions/Pending", {
            headers: {
              marketid: this.selectedCompanies.id,
            },
            params: {
              sDate: this.rptStartDate,
              eDate: this.rptEndDate,
            },
          })
          .then((response) => {
            this.transactionsPending = response.data.datas;
            this.transCount = response.data.total;
          })
          .catch((error) => console.log(error.response))
          .finally(() => {
            this.loading = false;
            this.empty = false;
          });
      }
    },
    getTransDetail(stnNum, transNum) {
      if (stnNum < 10) {
        this.transDetail = "0" + stnNum + transNum;
      } else {
        this.transDetail = stnNum + transNum;
      }
      if (this.transType == "Regular") {
        axios
          .get("https://cloudbackofficereportapidev.azurewebsites.net/Report/Transactions/Detail/" + this.transDetail, {
            headers: {
              marketid: this.selectedCompanies.id,
            },
          })
          .then((response) => {
            this.transactionDetailData = response.data.datas;
          })
          .catch((error) => console.log(error.response))
          .finally(() => (this.loading = false));
      } else if (this.transType == "Pending") {
        axios
          .get("https://cloudbackofficereportapidev.azurewebsites.net/Report/Transactions/Pending/Detail", {
            headers: {
              marketid: this.selectedCompanies.id,
            },
            params: {
              stn: stnNum,
              trn_no: transNum,
            },
          })
          .then((response) => {
            this.transactionPendingDetailData = response.data.datas;
          })
          .catch((error) => console.log(error.response))
          .finally(() => (this.loading = false));
      }
    },
    closePopup() {
      return (this.transDetail = false);
    },
  },
  computed: {
    fileName() {
      let excelName = this.$route.name + " " + this.startDate + "-" + this.endDate;
      return excelName;
    },
    fileNameDetail() {
      let excelDetailName = "Transaction Detail";
      return excelDetailName;
    },
    totalPendingTransAmount() {
      let total = 0;
      for (let transactionPending of this.transactionsPending) {
        total += transactionPending.total;
      }
      return total;
    },
    totalPendingTransDeposit() {
      let total = 0;
      for (let transactionPending of this.transactionsPending) {
        total += transactionPending.deposit;
      }
      return total;
    },
  },
};
</script>
